'use client';  // This marks the file as a client component

import Sidebar from '@/components/Sidebar';
import type { Metadata } from "next";
import "./globals.css";
import { useEffect } from 'react';

export const metadata: Metadata = {
  title: "MIX",
  description: "CRM by RAYDEV",
};

export default function RootLayout({ children }: { children: React.ReactNode }) {

  useEffect(() => {
    // Detect color scheme
    const prefersDarkMode = window.matchMedia('(prefers-color-scheme: dark)').matches;
    const favicon = document.getElementById('favicon') as HTMLLinkElement;

    // Change favicon based on dark mode
    if (prefersDarkMode) {
      favicon.href = "/mixMv2.svg";  // Dark mode logo
    } else {
      favicon.href = "/mixMv1.svg";  // Light mode logo
    }

    // Listen for changes in the color scheme and update the favicon accordingly
    window.matchMedia('(prefers-color-scheme: dark)').addEventListener('change', (e) => {
      if (e.matches) {
        favicon.href = "/mixMv2.svg";  // Dark mode logo
      } else {
        favicon.href = "/mixMv1.svg";  // Light mode logo
      }
    });
  }, []);

  return (
    <html lang="en">
      <head>
        <link 
          id="favicon" 
          rel="icon" 
          href="/mixMv1.svg"  // Default favicon (light mode)
          type="image/svg+xml" 
        />
      </head>
      <body className="min-h-screen flex">
        <div className="min-h-screen w-full flex">
          <aside className="flex-shrink-0 sticky top-0 h-screen">
            <Sidebar />
          </aside>
          <main className="flex-grow overflow-y-auto">
            <div className="sticky top-0 z-10 flex items-center justify-end p-4 pr-5 bg-gray-800 text-white shadow-md">
              <img src="/profile.jpg" alt="Profile picture" className="w-8 h-8 rounded-full mr-2"/>
              <span className="font-bold text-xl">Mary Sue</span>
            </div>
            <div className="pt-10">
              {children}
            </div>
          </main>
        </div>
      </body>
    </html>
  );
}
